    font-family: 'Lato';
    src: local('Lato'), url(./fonts/Lato/Lato-Italic.ttf) format('truetype');
    font-style: italic;
}
@font-face {
    font-family: 'Lato';
    src: local('Lato'), url(./fonts/Lato/Lato-Bold.ttf) format('truetype');
